/* eslint-disable max-len */
import { NgClass } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { Router, RouterModule } from '@angular/router';
import { User } from '@biz-nest/auth-client';
import { Company } from '@biz-nest/commerce-client';
import { FrontendLogger } from '@biz-nest/core-client';
import { CurrencyService } from '@front-shared-lib';
import { MailUser } from '@main-shared-lib';

// eslint-disable-next-line @typescript-eslint/naming-convention
declare let ApexCharts;

@Component({
  selector: 'app-dash-cards',
  standalone: true,
  imports: [
    FormsModule,
    ReactiveFormsModule,
    RouterModule,
    NgClass
  ],
  templateUrl: './dash-cards.component.html'
})
export class DashCardsComponent
implements OnInit {
  countCompanys = 0;
  usersCount = 0;
  companys: Company[] = [];
  loadingCompanys = false;
  getCompanyErr?: string;
  companysNums: number[] = [];
  labelsChaart2: string[] = [];
  loadingUsers = false;
  getUsersErr?: string;
  mailAccountCount = 0;
  companysGrowth = 0;
  usersGrowth = 0;
  userGrowth = '';

  constructor(
    public currencyService: CurrencyService,
    private router: Router
  ) {}

  ngOnInit(): void {
    this.getCompanys();
    this.initCompanyChat();
    this.getCompanyGrowthDiff();
    this.getUsers();
    this.initUserChat();
    this.getMails();
    this.getUserGrowthDiff();
  }

  navigate(route: string) {
    this.router.navigate([route]);
  }

  async getCompanys() {
    try {
      this.loadingCompanys = true;
      this.getCompanyErr = '';
      const { count, dataArr } = await Company
        .filterAll({ returnEmptyArray: true });

      this.loadingCompanys = false;
      if (this.getCompanyErr) {
        return;
      }
      this.countCompanys = count;
      this.companys = dataArr;
      this.companysNums = this.companys.map((val, index) => index);
      this.labelsChaart2 = this.companys.map((val, index) => index.toString());
    } catch (error) {
      FrontendLogger.error(error);
    }
  }

  async getUsers() {
    try {
      this.loadingUsers = true;
      this.getUsersErr = '';
      const { count } = await User
        .filterAll({
          returnEmptyArray: true
        });

      this.loadingUsers = false;
      if (this.getUsersErr) {
        return;
      }
      this.usersCount = count;
    } catch (error) {
      FrontendLogger.error(error);
    }
  }


  async getMails() {
    try {
      const mails = await MailUser
        .getAll();

      this.mailAccountCount = mails.length;
    } catch (error) {
      FrontendLogger.error(error);
    }
  }

  async getCompanyGrowthDiff() {
    try {
      const lastMonth = await Company.filterAll({
        comparisonFilter: [
          {
            field: 'createdAt',
            fieldValue: new Date(new Date().getFullYear(), new Date().getMonth() - 1, 1),
            operator: 'gte'
          },
          {
            field: 'createdAt',
            fieldValue: new Date(new Date().getFullYear(), new Date().getMonth(), 0),
            operator: 'lte'
          }
        ]
      });

      const thisMonth = await Company.filterAll({
        comparisonFilter: [
          {
            field: 'createdAt',
            fieldValue: new Date(new Date().getFullYear(), new Date().getMonth(), 1),
            operator: 'gte'
          },
          {
            field: 'createdAt',
            fieldValue: new Date(new Date().getFullYear(), new Date().getMonth() + 1, 0),
            operator: 'lte'
          }
        ]
      });

      this.companysGrowth = ((thisMonth.count - lastMonth.count) / (lastMonth.count || 1) * 100) || 0;
    } catch (error) {
      FrontendLogger.error(error);
    }
  }

  async getUserGrowthDiff() {
    try {
      const lastMonth = await User.filterAll({
        comparisonFilter: [
          {
            field: 'createdAt',
            fieldValue: new Date(new Date().getFullYear(), new Date().getMonth() - 1, 1),
            operator: 'gte'
          },
          {
            field: 'createdAt',
            fieldValue: new Date(new Date().getFullYear(), new Date().getMonth(), 0),
            operator: 'lte'
          }
        ]
      });

      const thisMonth = await User.filterAll({
        comparisonFilter: [
          {
            field: 'createdAt',
            fieldValue: new Date(new Date().getFullYear(), new Date().getMonth(), 1),
            operator: 'gte'
          },
          {
            field: 'createdAt',
            fieldValue: new Date(new Date().getFullYear(), new Date().getMonth() + 1, 0),
            operator: 'lte'
          }
        ]
      });

      this.usersGrowth = ((thisMonth.count - lastMonth.count) / (lastMonth.count || 1) * 100) || 0;
    } catch (error) {
      FrontendLogger.error(error);
    }
  }

  async initCompanyChat() {
    const months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];

    const promises = months.map((month, index) => Company.filterAll({
      comparisonFilter: [{
        field: 'createdAt',
        fieldValue: new Date(new Date().getFullYear(), index, 1),
        operator: 'gte'
      },
      {
        field: 'createdAt',
        fieldValue: new Date(new Date().getFullYear(), index + 1, 0),
        operator: 'lte'
      }]
    }));

    const all = await Promise.all(promises);

    const data = all.map(val => val.dataArr.length);

    const options = {
      series: [{
        data
      }],
      chart: {
        type: 'line',
        height: 50,
        sparkline: {
          enabled: true
        }
      },
      colors: ['#5156be'],
      stroke: {
        curve: 'smooth',
        width: 2
      },
      tooltip: {
        fixed: {
          enabled: false
        },
        x: {
          show: false
        },
        y: {
          title: {
            formatter: function() {
              return '';
            }
          }
        },
        marker: {
          show: false
        }
      }
    };

    const chart = new ApexCharts(document.querySelector('#company-chart'), options);

    chart.render();
  }


  async initUserChat() {
    const months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];

    const promises = months.map((month, index) => User.filterAll({
      comparisonFilter: [{
        field: 'createdAt',
        fieldValue: new Date(new Date().getFullYear(), index, 1),
        operator: 'gte'
      },
      {
        field: 'createdAt',
        fieldValue: new Date(new Date().getFullYear(), index + 1, 0),
        operator: 'lte'
      }]
    }));

    const all = await Promise.all(promises);

    const data = all.map(val => val.dataArr.length);

    const options = {
      series: [{
        data
      }],
      chart: {
        type: 'line',
        height: 50,
        sparkline: {
          enabled: true
        }
      },
      colors: ['#4CAF50', '#2196F3', '#FF9800'],
      stroke: {
        curve: 'smooth',
        width: 2
      },
      tooltip: {
        fixed: {
          enabled: false
        },
        x: {
          show: false
        },
        y: {
          title: {
            formatter: function() {
              return '';
            }
          }
        },
        marker: {
          show: false
        }
      }
    };

    const chart = new ApexCharts(document.querySelector('#user-chart'), options);

    chart.render();
  }
}
