import { AfterViewInit, Component, OnInit } from '@angular/core';
import { Meta, Title } from '@angular/platform-browser';
import { RouterModule } from '@angular/router';
import {
  AppNameService, MainBarTitleService,
  scrollToTop, WindowService
} from '@front-shared-lib';
import { AdminBreadcrumbComponent } from '../../misc/admin-breadcrumb/admin-breadcrumb.component';
import { AdminPageContentComponent } from '../../misc/admin-page-content/admin-page-content.component';
import { CompanyAnalyticsComponent } from '../home-comps/company-analytics/company-analytics.component';
import { DashCardsComponent } from '../home-comps/dash-cards/dash-cards.component';
import {
  EcommerceRevenueAnalyticsComponent
} from '../home-comps/ecommerce-revenue-analytics/ecommerce-revenue-analytics.component';
import {
  SubscriptionOverviewComponent
} from '../home-comps/subscription-overview/subscription-overview.component';

@Component({
  selector: 'app-home',
  standalone: true,
  imports: [
    RouterModule,
    CompanyAnalyticsComponent,
    DashCardsComponent,
    EcommerceRevenueAnalyticsComponent,
    SubscriptionOverviewComponent,
    AdminPageContentComponent,
    AdminBreadcrumbComponent
  ],
  templateUrl: './home.component.html',
  styleUrl: './home.component.scss'
})
export class HomeComponent
implements OnInit, AfterViewInit {
  constructor(
    private windowService: WindowService,
    private apNme: AppNameService,
    public mainBarTitle: MainBarTitleService,
    private metaTagService: Meta,
    private titleService: Title
  ) {}

  ngOnInit(): void {
    this.mainBarTitle.title = 'Dashboard - Manage Vendors, users, Managers, and more';
    this.titleService.setTitle(this.mainBarTitle.title + ' - ' + this.apNme.name);
    this.metaTagService.updateTag({
      name: 'description',
      content: 'Overview of your dashboard for quick access to key metrics and insights.'
    });
  }

  ngAfterViewInit() {
    scrollToTop(this.windowService);
  }
}


