import { CurrencyPipe } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';
import { Company, Item } from '@biz-nest/commerce-client';
import { FrontendLogger } from '@biz-nest/core-client';
import { CurrencyService } from '@front-shared-lib';
import { EagleInfoClientError } from '@main-shared-lib';

@Component({
  selector: 'app-company-analytics',
  standalone: true,
  imports: [
    FormsModule,
    ReactiveFormsModule,
    RouterModule,
    CurrencyPipe
  ],
  templateUrl: './company-analytics.component.html'
})
export class CompanyAnalyticsComponent
implements OnInit {
  highestSellingItem?: Item;
  highestSellingCompany?: Company;
  totalCompanies = 0;

  constructor(public currencyService: CurrencyService) {}

  ngOnInit(): void {
    this.getTotalCompanys();
    this.getHigehestSellingItem();
  }

  async getTotalCompanys() {
    try {
      const { count } = await Company.filterAll({
        returnEmptyArray: true
      });

      this.totalCompanies = count;
    } catch (error) {
      FrontendLogger.error(error);
    }
  }

  async getHigehestSellingItem() {
    try {
      const { dataArr } = await Item.filterAll({
        offset: 0,
        limit: 1,
        propSort: {
          soldCount: 'desc'
        }
      });

      if (dataArr[0]) {
        this.highestSellingItem = dataArr[0];
        this.getHigehestSellingCompany();
      }
    } catch (error) {
      FrontendLogger.error(error);
    }
  }

  async getHigehestSellingCompany() {
    if (!this.highestSellingItem?.company) {
      throw new EagleInfoClientError('highestSellingItem does not have a company');
    }

    try {
      const company = await Company.getOne(this.highestSellingItem?.company);

      if (company) {
        this.highestSellingCompany = company;
      }
    } catch (error) {
      FrontendLogger.error(error);
    }
  }
}
