/* eslint-disable max-len */
import { CurrencyPipe } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { Router, RouterModule } from '@angular/router';
import { CompanySubscription } from '@biz-nest/commerce-client';
import { FrontendLogger } from '@biz-nest/core-client';
import { CurrencyService, TooltipComponent } from '@front-shared-lib';
// eslint-disable-next-line @typescript-eslint/naming-convention
declare const ApexCharts;
declare const $;

@Component({
  selector: 'app-subscription-overview',
  standalone: true,
  imports: [
    FormsModule,
    ReactiveFormsModule,
    CurrencyPipe,
    RouterModule,
    TooltipComponent
  ],
  templateUrl: './subscription-overview.component.html'
})
export class SubscriptionOverviewComponent
implements OnInit {
  subscriptionTotal = 0;
  subscriptionFreeTotal = 0;
  subscriptionPaidTotal = 0;
  monthsOfYear = [{
    name: 'Jan',
    value: 0
  },
  {
    name: 'Feb',
    value: 1
  },
  {
    name: 'Mar',
    value: 2
  },
  {
    name: 'Apr',
    value: 3
  },
  {
    name: 'May',
    value: 4
  },
  {
    name: 'Jun',
    value: 5
  },
  {
    name: 'Jul',
    value: 6
  },
  {
    name: 'Aug',
    value: 7
  },
  {
    name: 'Sep',
    value: 8
  },
  {
    name: 'Oct',
    value: 9
  },
  {
    name: 'Nov',
    value: 10
  },
  {
    name: 'Dec',
    value: 11
  }];

  chart;

  todayMonth = new Date().getMonth();

  constructor(
    public currencyService: CurrencyService,
    private router: Router
  ) {}

  ngOnInit(): void {
    this.getSubscriptions();
    this.getCompanyByCreationMonth(this.todayMonth);
  }

  async getSubscriptions() {
    try {
      const { dataArr } = await CompanySubscription
        .filterAll({});

      FrontendLogger.debug('getSubscriptions, ', dataArr);

      this.subscriptionTotal = (dataArr).reduce((acc, val) => val.amount + acc, 0);
      this.subscriptionFreeTotal = (dataArr)
        .filter(val => val.name === 'Free').reduce((acc, val) => val.amount + acc, 0);
      this.subscriptionPaidTotal = (dataArr)
        .filter(val => val.name !== 'Free').reduce((acc, val) => val.amount + acc, 0);
    } catch (error) {
      FrontendLogger.error(error);
    }
  }


  async getCompanyByCreationYears() {
    const months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];

    const promises = months.map((month, index) => CompanySubscription.filterAll({
      comparisonFilter: [{
        field: 'createdAt',
        fieldValue: new Date(new Date().getFullYear(), index, 1),
        operator: 'gte'
      },
      {
        field: 'createdAt',
        fieldValue: new Date(new Date().getFullYear(), index + 1, 0),
        operator: 'lte'
      }]
    }));

    const all = await Promise.all(promises);

    const data = all.map(val => val.dataArr.length);

    this.renderCompanyAnalytics(data, months);
  }

  async getCompanyByCreationMonth(month: number) {
    FrontendLogger.debug('getCompanyByCreationMonth, ', month);
    const daysInMonth = Array
      .from(Array(new Date(new Date().getFullYear(), month + 1, 0).getDate()).keys()).map(x => x + 1);

    const promises = daysInMonth.map(day => {
      return CompanySubscription.filterAll({
        comparisonFilter: [{
          field: 'createdAt',
          fieldValue: new Date(new Date().getFullYear(), month, day),
          operator: 'gte'
        },
        {
          field: 'createdAt',
          fieldValue: new Date(new Date().getFullYear(), month, day + 1),
          operator: 'lte'
        }]
      });});

    const all = await Promise.all(promises);

    const data = all.map(val => val.dataArr.length);

    const days = daysInMonth.map(day => day.toString());

    this.renderCompanyAnalytics(data, days);
  }

  renderCompanyAnalytics(data: number[], categories: string[]) {
    FrontendLogger.debug('rendering data, ', data);

    const options = {
      series: [{
        name: 'Subscriptions',
        data: [
          ...data
        ]
      }],
      chart: {
        type: 'area',
        height: '100%',
        width: '100%',
        parentHeightOffset: 0,
        sparkline: {
          enabled: false
        },
        toolbar: {
          show: false
        }
      },
      plotOptions: {
        bar: {
          columnWidth: '20%'
        }
      },
      colors: ['#4CAF50', '#2196F3', '#FF9800'],
      fill: {
        opacity: 1
      },
      stroke: {
        curve: 'smooth',
        width: 2
      },
      tooltip: {
        fixed: {
          enabled: false
        },
        x: {
          show: false
        },
        y: {
          title: {
            formatter: function() {
              return '';
            }
          }
        },
        marker: {
          show: false
        }
      },
      yaxis: {
        labels: {
          formatter: function(y) {
            return y.toFixed(0) + '%';
          }
        },
        axisBorder: {
          show: true
        },
        axisTicks: {
          show: true
        }
      },
      xaxis: {
        categories,
        labels: {
          rotate: -90
        },
        axisBorder: {
          show: true
        },
        axisTicks: {
          show: true
        }
      },
      grid: {
        show: true,
        borderColor: '#e0e0e0',
        strokeDashArray: 2,
        position: 'back',
        xaxis: {
          lines: {
            show: true
          }
        },
        yaxis: {
          lines: {
            show: true
          }
        }
      }
    };

    if (this.chart) {
      this.chart.updateOptions(options);

      return;
    }


    this.chart = new ApexCharts(document.querySelector('#subscription-chart'), options);

    this.chart.render();
  }

  navigate(route: string) {
    this.router.navigate([route]);
  }
}
